body {
    margin: 0;
    font-family: Arial, sans-serif;
    height: 100vh;
    overflow-y: auto;
}

.main-container {
    padding: 0;
    text-align: center;
    border: 0.5px solid rgba(51, 51, 51, 0.5); /* 테두리 두께를 0.5px로 설정 */
    max-width: 430px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    box-sizing: border-box;
}

.main-title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1.5rem;
}

.card-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: auto;
}

.card {
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    padding: 1.5rem;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    width: 100%;
    box-sizing: border-box;
}

@media (min-width: 600px) {
    .main-container {
        padding: 3rem;
        margin: 0 auto;
    }

    .main-title {
        font-size: 2.5rem;
    }

    .card {
        padding: 2rem;
    }
}

@media (min-width: 900px) {
    .card-container {
        flex-direction: column;
    }

    .card {
        margin: 0;
    }
}
